import React, {useState} from 'react';
import {connect} from 'react-redux';

import {selectSearch} from '../../store/reducers';
import {searchActions} from '../../store/actions';

import {Panel, Search, Cell} from '@vkontakte/vkui';

import RefreshList from '../../components/RefreshList';
import HeaderPanel from '../../components/HeaderPanel';

const Searchs = ({id, go, dispatch, fetchedSearch}) => {
	const [isFetching, setIsFetching] = useState(false);
	const [search, setSearch] = useState('');
	const {fetchenSearch} = searchActions;

	const onRefresh = () => {
		setIsFetching(true);
		setTimeout(() => {
			dispatch(fetchenSearch({
				search,
				filters: 'friends',
				fields: 'crop_photo'
			}));
			setIsFetching(false);
		}, 2000);
	};

	const handleSearch = (q) => {
		setSearch(q);
		dispatch(fetchenSearch({
			q,
			filters: 'friends',
			fields: 'crop_photo'
		}));
	};

	return (
		<Panel id={id}>
			<HeaderPanel handelClick={go} dataTo='home' title='Поиск' />
			<Search value={search} onChange={handleSearch}/>

			<RefreshList
				handleRefresh={onRefresh}
				isFetching={isFetching}
				list={fetchedSearch}
				footerNameCount='найдено'
			>
				<ListItem />
			</RefreshList>
		</Panel>
	);
};

const ListItem = ({profile: {first_name, last_name, id}, description}) => {
	return (
		<Cell
			key={id}
			description={description}
			onClick={() => window.location.assign(`https://vk.com/id${id}`)}
		>
			{`${first_name} ${last_name}`}
		</Cell>
	);
};

const mapStateToProps = (state) => ({
	fetchedSearch: selectSearch.getSearch(state)
});

export default connect(mapStateToProps, null)(Searchs);