import connect from '@vkontakte/vkui-connect-promise';

const v = '5.95';
const app_id = 7080438;

const fetchedAccessToken = async (scope) => {
	try {
		const response = await connect.send('VKWebAppGetAuthToken', {app_id, scope});
		return await response.data.access_token;
	} catch (err) {
		throw Error(err.message);
	}
};

const getAccessToken = async (scope) => {
	try {
		const resTime = 1000000;
		const newTime = Date.now();
		const oldTime = localStorage.dateTime || (Date.now() - resTime + 1);
		const diffTime = newTime - oldTime;
		const updateScope = localStorage.scope !== scope;
		const oneFetched = !localStorage.oneFetched;
		console.log('++++++++++')
		if ((!localStorage.access_token && diffTime > resTime || updateScope) && oneFetched) {
			console.log('--------------')
			const access_token = await fetchedAccessToken(scope);
			localStorage.access_token = access_token;
			localStorage.dateTime = newTime;
			localStorage.scope = scope;
		}
		return await localStorage.access_token;
	} catch (err) {
		localStorage.access_token = null;
		localStorage.oneFetched = null;
		localStorage.scope = null;
		localStorage.dateTime = null;
		throw Error(err.message);
	}
};

export const getUser = async () => {
	try{
		const response = await connect.send('VKWebAppGetUserInfo', {});
		return await response.data;
	} catch(err) {
		throw Error(err.message);
	}
};

export const getFriends = async (count=100, offset=0) => {
	try{
		const access_token = await getAccessToken('friends');
		const method = 'friends.get';
		const fields = 'nickname,domain,sex,bdate,city,country,timezone,photo_50,photo_100,photo_200_orig,has_mobile,contacts,education,online,relation,last_seen,status,can_write_private_message,can_see_all_posts,can_post,universities';
		const params = {access_token, v, fields, count, offset};
		const response = await connect.send('VKWebAppCallAPIMethod', {method, params});
		return await response.data.response.items;
	} catch (err) {
		throw Error(err.message);
	}
};

export const fetchedSearch = async ({q='', offset=0, limit=10, filters='', fields='', search_global=1}) => {
	try{
		const access_token = await getAccessToken('friends, groups');
		const method = 'search.getHints';
		const params = { access_token, v, q, offset, limit, filters, search_global };
		const response = await connect.send('VKWebAppCallAPIMethod', {method, params});
		return await response.data.response.items;
	} catch (err) {
		throw Error(err.message);
	}
};