import React, {cloneElement} from 'react';
import PropTypes from 'prop-types';
import {Footer,PullToRefresh,Group,List,Spinner} from '@vkontakte/vkui';

const RefreshList = ({
	handleScroll,
	handleRefresh,
	isFetching,
	list,
	component,
	footerNameCount,
	groupTitle,
	children
}) => {
	
	return(
		<PullToRefresh
			onScroll={handleScroll}
			onRefresh={handleRefresh}
			isFetching={isFetching}>
			{!list.length && !isFetching && <div style={{ display: 'flex', alignItems: 'center', flexDirection: 'column' }}>
				<Spinner size="regular" style={{ marginTop: 20 }} />
			</div>}
			{list.length > 0 && <Group title={groupTitle}>
				{list.map((item, key) => (
					<List key={item.id || key}>
						{cloneElement(children, item)}
						<span></span>
					</List>
				))}
			</Group>}
			<Footer>{list.length} {footerNameCount}</Footer>
		</PullToRefresh>
	);
}

RefreshList.propTypes = {
	handleScroll: PropTypes.func,
	handleRefresh: PropTypes.func.isRequired,
	isFetching: PropTypes.bool.isRequired,
	list: PropTypes.array.isRequired,
	groupTitle: PropTypes.string,
	footerNameCount: PropTypes.string,
};

RefreshList.defaultProps = {
	handleScroll: () => {},
};

export default RefreshList;
